@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
.header {
  width: 100%;
  height: 85px;
  background-color: #6b068a;
  background: linear-gradient(90deg, rgba(83, 7, 106, 1) 0%, rgba(107, 6, 138, 1) 25%);
  position: fixed;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  z-index: 1;
  box-shadow: 1px 3px 8px -1px rgb(26, 25, 25);
}

.headerImage {
  width: 100%;
  height: 450px;
  overflow: hidden;
}

.headerImage img {
  width: 120%;
  height: 100%;
  object-fit: cover;
  transform: rotate(-7deg) translateY(-2rem) translateX(5rem);
}
.backgroundWindow {
  background-color: rgb(15, 15, 15);
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  filter: blur(8px);
  opacity: 0.7;
}

.modalWindowContainer {
  width: 40%;
  height: auto;
  padding: 20px;
  position: fixed;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 20px;
  box-shadow: -0px -0px 11px -1px rgba(4, 4, 4, 1);
  color: black;


  .emptyCart {
    text-align: center;
    font-weight: 400;
    font-size: 20px;
  }


  .TotalWrapper {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: bolder;
    margin: 30px 0px 10px 0px;
  }

  .buttonsWrapper {
    display: flex;
    justify-content: flex-end;
    grid-gap: 8px;
    gap: 8px;
  }

  .closeButton {
    cursor: pointer;
    align-items: center;
    width: 120px;
    height: 35px;
    background-color: #ffffff;
    border-radius: 30px;
    box-shadow: none;
    border: 1px solid #5b0279;
    color: #5b0279;
    font-weight: bold;
  }

  .buyButton {
    cursor: pointer;
    align-items: center;
    width: 120px;
    height: 35px;
    background-color: #5b0279;
    border-radius: 30px;
    box-shadow: none;
    border: none;
    color: white;
    font-weight: bold;
  }
}
.listContainer {
  overflow: auto;
  height: 300px;
}

.cartWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid #370049;
}

.cartValue {
  display: flex;
  justify-content: space-between;
  width: 150px;
  font-weight: bold;

  .totalPrice {
    color: #5b0279;
  }


  .totalAmount {
    border: 0.05px solid rgb(229 229 229);
    border-radius: 5px;
    width: 40px;
    text-align: center;
  }
}

.addRemoveWrapper {
  display: flex;
  grid-gap: 8px;
  gap: 8px;

  .container {
    width: 35px;
    height: 35px;
  }

  .material-icons {
    font-size: 35px;
    color: #5b0279;
  }

  .material-icons:hover {
    cursor: pointer;
  }

  .material-icons:active {
    font-size: 33px;
  }
}
button {
  cursor: pointer;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 230px;
  height: 35px;
  background-color: #47035d;
  border-radius: 30px;
  box-shadow: none;
  border: none;
  color: white;
  font-weight: bold;
}

.CartButton_numberAddedItems__3LWEG {
  padding: 5px 12px;
  border-radius: 40%;
  background-color: #a31bcc;
}

button:hover,
button:focus-visible {
  background-color: #2e043b;
}

button:focus-visible {
  outline: none;
}

button:hover .CartButton_numberAddedItems__3LWEG,
button:focus-visible .CartButton_numberAddedItems__3LWEG {
  background-color: #661280;
}

.CartButton_bump__uR-Xa {
  animation: CartButton_bump__uR-Xa 300ms ease-out;
}

@keyframes CartButton_bump__uR-Xa {
  0% {
    transform: scale(1);
  }

  10% {
    transform: scale(0.9);
  }

  30% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(1.15);
  }

  100% {
    transform: scale(1);
  }
}
.descriptionCard {
  width: 49%;
  margin: 0 auto;
  text-align: center;
  background-color: rgba(107, 6, 138, 0.9);
  transform: translateY(-12rem);
  border-radius: 20px;
  color: white;
  padding: 10px 10px;
  box-shadow: -0px -0px 11px -1px rgba(4, 4, 4, 1);
}

.descriptionCard h2 {
  margin-top: 0px;
}

section {
  width: 60%;
  margin: -150px auto 50px auto;
  border: 1.5px solid rgb(223, 221, 221);
  border-radius: 20px;
  box-shadow: 0px 0px 11px 1px rgb(223, 221, 221);
  padding: 20px 0px;
}

ul.listFood {
  list-style-type: none;
}
.oneItem {
  display: flex;
  justify-content: space-between;
  width: 94%;
  margin-top: 20px;
  border-bottom: 1.5px solid rgb(223, 221, 221);

  p,
  h3 {
    margin: 4px;
  }

  .description {
    font-style: italic;
  }

  .price {
    font-weight: bolder;
    font-size: large;
    margin-bottom: 15px;
    padding-bottom: 20px;
    color: rgba(107, 6, 138, 0.9);

  }
}
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
}

.MealItemForm_buttonAdd__1PViO {
  cursor: pointer;
  align-items: center;
  width: 120px;
  height: 35px;
  background-color: #5b0279;
  border-radius: 30px;
  box-shadow: none;
  border: none;
  color: white;
  font-weight: bold;
}

.MealItemForm_amountWrapper__REXlU {
  display: flex;
  align-items: baseline;
  grid-gap: 8px;
  gap: 8px;

  label {
    font-weight: bold;
    font-size: 15px;
    margin: 5px;
  }

  input {
    width: 50px;
    height: 25px;
    border-radius: 5px;
    text-align: center;
    border: 1px solid rgb(197, 197, 197);
  }

}
